<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <new-pizza></new-pizza>
    </div>
    <!--new pizza-->
    <div class="col-sm-12 col-md-4">
      <h3 class="text-muted my-5">菜单</h3>
      <table class="table">
        <thead class="table table-default">
        <tr>
          <th>品种</th>
          <th>删除</th>
        </tr>
        </thead>
        <tbody v-for="item in getMinuItems">
        <tr>
          <td>{{item.name}}</td>
          <td>
            <button @click="del(item)" class="btn btn-outline-danger btn-sm">&times;</button>
          </td>
        </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>
<script>
  import NewPizza from './NewPizza'
  import Cookie from 'js-cookie'
  export default {
    components: {
      NewPizza
    },
    data:function(){
      return {
        // getMinuItems:[]
      }
    },
    created(){
      console.log(Cookie.get('name'))
      fetch("https://wd3780466709glsyyc.wilddogio.com/menu1.json")
        .then(res => res.json())
        .then( data =>{
          console.log(data)
          let newArr = [];
          for(let key in data){
            data[key].id = key;
            newArr.push(data[key])
          }
          // 数据同步
          this.$store.commit('setMenuItems',newArr);
          // console.log(newArr)
          // this.getMinuItems = newArr;
          // console.log(111)
          // console.log(this.getMinuItems)
        })
    },
    methods:{
      del(item){
        fetch(`https://wd3780466709glsyyc.wilddogio.com/menu1/${item.id}/.json`,{
          method:"DELETE",
          headers:{
            "Content-type":'application/json'
          }
        })
          .then(res => res.json())
          .then( data => {
            // this.$router.push({path:'/menu'})
            this.$store.commit('removeMenuItems',item)
          })
          .catch(err => console.log(err))
      }
    },
    computed:{
      getMinuItems:{
        //在vuex中获取数据
        get(){
          console.log(this.$store.state.menuItems)
          // return this.$store.state.menuItems;
// 通过getters获取数据，这样store就不会暴露出来，安全一些
          return this.$store.getters.getMenuItems
        },
        set(){

        }
      },

    }
  }
</script>



