<template>
  <form>
    <h3 class="text-muted my-5" @click="togo">添加新pizza</h3>
    <div class="form-group row">
      <label class="col-sm-1">品种</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.name">
      </div>
    </div>

    <div class="form-group row">
      <label class="col-sm-1">描述</label>
      <div class="col-sm-11">
        <textarea rows="5" class="form-control" v-model="newPizza.description"></textarea>
      </div>
    </div>
    <p><s>选项1</s></p>
    <div class="form-group row">
      <label class="col-sm-1">尺寸</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.size1">
      </div>
    </div>
    <div class="form-group row">
      <label class="col-sm-1">价格</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.price1">
      </div>
    </div>
    <p><s>选项2</s></p>
    <div class="form-group row">
      <label class="col-sm-1">尺寸</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.size2">
      </div>
    </div>
    <div class="form-group row">
      <label class="col-sm-1">价格</label>
      <div class="col-sm-11">
        <input type="text" class="form-control" v-model="newPizza.price2">
      </div>
    </div>
    <button type="button" class="btn btn-success btn-block" @click="add">添加</button>
  </form>
</template>

<script>
  export default {
    name: "NewPizza",
    data: function () {
      return {
        newPizza:{
          name:'',
          description:'',
          size1:'',
          price1:'',
          size2:'',
          price2:''

        }
      }
    },
    methods:{
      add(){
        let data = {
          name:this.newPizza.name,
          description:this.newPizza.description,
          options:[
            {
              size:this.newPizza.size1,
              price:this.newPizza.price1
            },
            {
              size:this.newPizza.size2,
              price:this.newPizza.price2
            }
          ]
        };
        fetch("https://wd3780466709glsyyc.wilddogio.com/menu1.json",{
          method:"POST",
          headers:{
            "Content-type":'application/json'
          },
          body:JSON.stringify(data)
        })
          .then(res => res.json())
          .then(data => {
            console.log(data);
            // this.$router.push({path:'/menu'});
            // 数据同步到vuex中
            this.$store.commit('pushToMenuItems',data);
          })

            .catch(err => console.log(err))
      },
      togo(){
        this.$router.push({path:'/menu'})
      }
    }
  }
</script>

<style scoped>

</style>



