<template>
  <div class="row">
    <div class="col-sm-12 text-center" id="home">
      <div class="background">
        <h1>欢迎大家品尝pizza</h1>
        <h2>这里有你喜欢的</h2>
        <button  class="btn btn-sucess" @click="gotoMenu">订餐</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    methods:{
      gotoMenu(){
        this.$router.push({path:'/menu'})
      }
    }
  }
</script>

<style>
  #home{
    background: url("../../src/assets/pizza.jpg");
    height:85vh;
    padding:10%;
  }
  h1,h2{
    margin:6%;
  }
  .background{
    background:#eee;
    opacity:0.8;
    max-width:70vw;
    margin:0 auto;
    padding:20px 0;
  }
</style>



