<template>
  <div class="row mt-3">
    <div class="col-md-12 col-lg-12">
      <div class="card">
        <div class="card-body">
          <img src="../../static/icon.png" alt="" class="mx-auto d-block">
          <form @submit.prevent="onSubmit">
            <div class="form-group">
              <label for="email">邮箱</label>
              <input
                type="email"
                class="form-control"
                v-model="email"
              >
            </div>
            <div class="form-group">
              <label for="password">密码</label>
              <input
                type="password"
                class="form-control"
                v-model="password"
              >
            </div>
            <button class="btn btn-block btn-success">登录</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import Cookies from 'js-cookie'

  export default {
    data:function(){
      return {
        email:'',
        password:'',
      }
    },
    // 组件内的导航守卫
    beforeRouteEnter:(to,from,next) => {
      next(vm => vm.$store.dispatch("setUser",null))
    }
    ,
    methods:{
      onSubmit(){
        const formData = {
          email:this.email,
          password:this.password
        };
        axios.get('https://wd3780466709glsyyc.wilddogio.com/user1.json')
          .then(res => {
            console.log(res)
            const arr = [];
            for(let key in res.data){
              arr.push(res.data[key])
            }
            console.log(arr)
            let result = arr.filter(user => {
              return user.email === this.email && this.password === user.password
            })
            if(result != '' && result.length > 0){
              this.$store.dispatch('setUser',result[0].email);
              Cookies.set('name', result[0].email);
              this.$router.push({path:'/'})
            }else{
              alert('账号或密码错误！')
              this.$store.dispatch('setUser',null);
            }
          });

      }
    }
  }
</script>



