<template>
  <div class="row">
    <!--菜单-->
    <div class="col-sm-12 col-md-8">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th>尺寸</th>
            <th>价格</th>
            <th>加入</th>
          </tr>
        </thead>
        <tbody v-for="item in getMenuItems">
        <tr>
          <td><s>{{item.name}}</s></td>
        </tr>
        <tr v-for="option in item.options">
          <td>{{option.size}}</td>
          <td>{{option.price}}</td>
          <td><button
            @click="addToBasket(item,option)"
            class="btn btn-sm btn-outline-success">+</button></td>
        </tr>
        </tbody>
      </table>
    </div>
    <!--购物车-->
    <div class="col-md-4 col-sm-12">
      <div v-if="basket.length > 0">
        <table class="table">
          <thead class="thead-default">
          <tr>
            <th>数量</th>
            <th>品种</th>
            <th>价格</th>
          </tr>
          </thead>
          <tbody v-for="item in basket">
          <tr>
            <td>
              <button @click="reduce(item)" class="btn btn-sm">-</button>
              <span>{{item.quantity}}</span>
              <button @click="add(item)" class="btn btn-sm">+</button>
            </td>
            <td>{{item.name}}{{item.size}}</td>
            <td>{{item.price * item.quantity}}</td>
          </tr>
          </tbody>
        </table>
        <p>总价：{{total + "RMB"}}</p>
        <button class="btn btn-block btn-success">提交</button>
      </div>
      <div v-else>
        {{basketText}}
      </div>
    </div>
    <!--{{basket}}-->
  </div>
</template>
<script>
  import axios from 'axios'

  export default {
    data:function(){
      return {
        basket:[],
        basketText:'购物车没有任何商品',
        // getMenuItems:{}
        // getMenuItems:{
        //   1: {
        //     'name': '榴莲pizza',
        //     'description': '这是喜欢吃榴莲朋友的最佳选择',
        //     'options': [{
        //       'size': 9,
        //       'price': 38
        //     }, {
        //       'size': 12,
        //       'price': 48
        //     }]
        //   },
        //   2: {
        //     'name': '芝士pizza',
        //     'description': '芝士杀手,浓浓的芝士丝, 食欲瞬间爆棚',
        //     'options': [{
        //       'size': 9,
        //       'price': 38
        //     }, {
        //       'size': 12,
        //       'price': 48
        //     }]
        //   },
        //   3: {
        //     'name': '夏威夷pizza',
        //     'description': '众多人的默认选择',
        //     'options': [{
        //       'size': 9,
        //       'price': 36
        //     }, {
        //       'size': 12,
        //       'price': 46
        //     }]
        //   }
        // }
      }
    },
    methods:{
      addToBasket(item,option){
        let bt = {
          name:item.name,
          size:option.size,
          price:option.price,
          quantity:1
        };
        console.log('--------')
        console.log(bt)
        if(this.basket.length > 0){
          // 过滤
          let result = this.basket.filter(bt => {
            return (bt.name === item.name && bt.price === option.price)
          });
          if(result != null && result.length > 0){
            result[0].quantity++;
          }else{
            this.basket.push(bt);
          }

        }else{
          this.basket.push(bt);
        }

      },
      add(item){
        item.quantity = item.quantity+1;
      },
      reduce(item){
        item.quantity = item.quantity-1;
        if(item.quantity === 0){
          this.remove(item);
        }
      },
      remove(item){
        this.basket.splice(this.basket.indexOf(item),1)
      },
      fetchData(){
        // fetch('https://wd5259196195tskwvr.wilddogio.com/menu1.json')
        //   .then(res => res.json())
        //   .then(data =>{
        //     console.log(data);
        //     this.getMenuItems = data;
        //   })
        // axios.get('https://wd5259196195tskwvr.wilddogio.com/menu1.json')
        //   .then(res => {
        //     this.getMenuItems = res.data;
        //   })
        // this.http.get('https://wd5259196195tskwvr.wilddogio.com/menu1.json')
        //   .then(res=>{
        //     this.getMenuItems = res.data;
        //   })
        // 将请求回来的数据寸到vuex中
        this.http.get('https://wd3780466709glsyyc.wilddogio.com/menu1.json')
          .then(res=>{
            // 请求下来的数据存到vuex
            this.$store.commit('setMenuItems',res.data);
          })
      }

    },
    computed:{
      total(){
        let totalCost = 0;
        for(let index in this.basket){
          totalCost += this.basket[index].price * this.basket[index].quantity;
        }
        return totalCost;
      },
      getMenuItems(){
        //在vuex中获取数据
        // return this.$store.state.menuItems;
        // 通过getters获取数据，这样store就不会暴露出来，安全一些
        return this.$store.getters.getMenuItems
      }
    },
    created(){
      this.fetchData();
    }
  }

</script>



