import Home from './components/Home'
import Menu from './components/Menu'
import Admin from './components/Admin'
import About from './components/about/About'
import Login from './components/Login'
import Register from './components/Register'

//about 下面的 二级路由
import Contact from './components/about/Contact'
import Delivery from './components/about/Delivery'
import History from './components/about/History'
import Order from './components/about/Order'

//三级路由
import Phone from './components/about/contact/Phone'
import PersonName from './components/about/contact/PersonName'

// const routes = [
export const routes = [
  // {path:'/',component:Home},
  {
    path: '/', components: {
      default: Home,
      'orderingGuide':Order,
      'delivery':Delivery,
      'history':History
    }
  },
  {path: '/menu', component: Menu},
  {
    path: '/about', component: About, redirect: '/contact', children: [
      {
        path: '/contact', component: Contact, redirect: '/phone', children: [
          {path: '/phone', component: Phone},
          {path: '/personname', component: PersonName},
          // {path:'*',redirect:'/phone'}
        ]
      },
      {path: '/delivery', component: Delivery},
      {path: '/history', component: History},
      {path: '/order', component: Order},
      // {path:'*',redirect:'/contact'}
    ]
  },
  {path: '/admin', component: Admin},
  {path: '/login', component: Login},
  {path: '/register', component: Register},
  {path: '*', redirect: '/'}
]



