const state = {
  currentUser:null,
  isLogin:false
}

const getters = {
  currentUser : state => state.currentUser,
  isLogin : state => state.isLogin
}

const mutations = {
// 更改用户的状态信息
  userStatus  (state,user)  {
    if(user){
      state.currentUser = user
      state.isLogin = true
    }else{
      state.currentUser = null
      state.isLogin = false
    }
  }
}

const actions = {
// 应用mutitions
  setUser({commit},user){
    //调用mutation里面的方法
    commit('userStatus',user)
  }

}

export default{
  state,
  getters,
  mutations,
  actions
}



